html {
  font-size: 62.5%;
}

html body #root {
  height: 100vh;
  width: 100vw;
}

.d-block {
  display: block;
}

.w-100 {
  width: 100%;
}

.h-100 {
  height: 100%;
}

.m-0 {
  margin: 0 !important;
}
.mt-0 {
  margin-top: 0 !important;
}
.mb-0 {
  margin-bottom: 0 !important;
}
.ml-0 {
  margin-left: 0 !important;
}
.mr-0 {
  margin-right: 0 !important;
}

.color-primary {
  color: #1890ff;
}

.color-danger {
  color: #ff4d4f;
}

.ant-form-item-explain-error * {
  text-align: left !important;
  font-size: 13px !important;
}
